<template>

    <!-- <div id="app" :style="{'background-image': `url(${require('@/assets/dj.jpg')})`}" 背景图片 太丑 遂放弃 -->
  <div id="app"     
    v-loading="loading" 
    element-loading-text="服务器正在努力下载 请稍等......"
    element-loading-spinner="el-icon-loading">

          <!-- 主页信息 -->
          <h2 align="center" style="color:#409EFF;">Respeed 🐳 Download</h2>
          <el-row type="flex" justify="center" align="middle" >

              <el-col :span="15" > <!--输入框框-->
                    <el-input placeholder="请输入要下载的资源URL (如：https://nginx.org/download/nginx-1.20.2.tar.gz)"  v-model="userURL" clearable @clear="restData"></el-input>
              </el-col>
              <el-col :span="4" style="display:flex; justify-content:center; align-items:center;">
                    <el-button  type="primary" @click="DownloadRes" >Download<i class="el-icon-download el-icon--right"></i></el-button>
                    
                    <el-button type="danger" size="mini" @click="toDonate" >支持<i class="el-icon-loading"></i></el-button>
              </el-col>
          </el-row>



            <!--文件（下载后）信息卡片 默认不显示-->

            <el-card class="box-card" v-if="CardShow"> 
                <div slot="header" class="clearfix" style="font-size:13px">
                  <span>文件信息</span>
                </div>

                <p style="font-size:13px"><el-tag effect="plain" size="small">文 件:</el-tag> {{ downloadURL }} 
                <el-button  type="primary" plain size="mini" id="copyBtn" :data-clipboard-text="downloadURL" data-clipboard-action="copy" @click="copyUrl">复制链接</el-button>
                </p>
                <p style="font-size:13px">
                  <el-tag effect="plain" size="small">Size:</el-tag> {{ size }} {{ sizeunit }}
                  <el-tag effect="plain" size="small">md5:</el-tag> {{ md5 }}
                </p>

                <el-link style="float: right; margin-bottom: 15px;" :underline="false" :href="downloadURL" >
                      <el-button  type="primary" plain size="small" >下载到本地<i class="el-icon-download el-icon--right"></i></el-button>
                </el-link>
            </el-card>


          <!--最近下载的文件列表 默认显示 有文件下载关闭显示-->
          <el-row type="flex" justify="center" align="middle" v-if="RecentTableShow">
            <el-col :span="16">
              <h5>
                  <a align="left" style="color:#409EFF;">最近下载</a>
                  <a align="left" style="color:#F56C6C;">(2小时后自动删除)</a>
              </h5>

              <el-table :data="fileListdata" stripe border>
                
                <el-table-column prop="Filename" label="文件">
                  <template slot-scope="scope">
                    
                    <a :href="scope.row.Dlurl" class="RentFileLlink">{{scope.row.Filename}}</a>
                    <i class="el-icon-download"></i>
                  </template>
                </el-table-column>
                
                <el-table-column prop="Size" label="大小" width="120"></el-table-column>
                <!-- <el-table-column prop="MD5" label="MD5" width="290"></el-table-column> -->
              </el-table>
              <!-- <el-progress :percentage="29" ></el-progress> -->

            </el-col>
          </el-row>
          
        <!-- <div class="donate_bgn"> <el-button type="danger" size="mini" @click="toDonate" >支持</el-button> </div>
        <div class="donate_img" v-if="imgShow">
          <img height="150px" src="https://7niu.chromiumer.com/donate.jpg">
        </div> -->

      
      <div class="footer">
          <pre>
               说明：
                   1.本站主要用于文件下载加速（国内资源下载可能会更慢）。
                   2.对下载的文件不做任何修改，并且下载2小时后服务器会自动将其删除，请及时下载到本地。
          </pre>
      </div>

      <!-- From:[中国 北京市 北石槽] 120.244.46.133 -->
      <div class="ipinfo"> {{ GeoIPContry }}  {{ GeoIPSubCity }} {{ GeoIPCityName }} {{ QueryIP }}
      </div>


  </div>

</template>

<script>
import Clipboard from "clipboard";

export default {

data() {

    return {
      //用户输入url
      userURL: "",
      //card 是否显示
      CardShow: false,
      loading: false,
      downloadURL: "",
      filename: "",
      md5: "",
      size: "",
      sizeunit: "",
      imgShow: false,
      RecentTableShow: true, //最近下载文件列表表格是否显示
      fileListdata:[],
      QueryIP: "",
      GeoIPContry: "",
      GeoIPSubCity: "",
      GeoIPCityName: ""

    }
  },
//初始化页面，获取最近下载文件列表信息
created () {
    this.getRentFileList()
    this.getMyIPInfo()
},
methods: {

    //清除下载数据
    restData() {
      this.CardShow = false
      this.downloadURL = ""
      this.filename = ""
      this.md5 = ""
      this.size = ""
      this.sizeunit = ""
    },
    //显示收款二维码窗口
    toDonate(){

        //this.imgShow = true
        if(this.imgShow){
						this.imgShow=false
					}else{
						// this.imgShow=true
            this.$alert('<img align="center" height="150px" src="https://static.chromiumer.com/donate.jpg">', '', {
              dangerouslyUseHTMLString: true,
              confirmButtonText: "感谢支持🙏",
              center: true,
              showClose: true
            });
				}

      },
    //执行下载操作
    DownloadRes() {

      const regURL = /^http[s]{0,1}:\/\/([\w.]+\/?)\S*/

      if(!regURL.test(this.userURL)) {

            // return this.$message.error('请输入有效的资源URL!!')
            return this.$notify.error({
              title: 'Oooooops!!',
              message: '请输入有效的资源URL!!',
              position: 'bottom-right',
              showClose: false
            });

      }else if( this.userURL === '' ) {

            return this.$message.success('请输入要下载的资源URL!!')

      }else {

            this.loading = true

            this.$http.get('/dl?url=' + this.userURL ).then(res => {

                if (res.status !== 200 ) {

                    return this.$message.error('Resources Request Failed, Please try again later !!! ')

                }else{

                    this.downloadURL =  res.data.dlurl
                    this.filename =  res.data.filename
                    this.md5 =  res.data.md5
                    this.size =  res.data.size
                    this.sizeunit =  res.data.sizeunit

                    this.RecentTableShow = false //关闭最近下载表格显示
                    this.loading = false //关闭遮罩显示
                    this.CardShow = true //显示下载的文件信息卡片

                    // 自动下载
                    // this.$http({
                    //   url: this.downloadURL,
                    //   method: 'GET',
                    //   responseType: 'blob', // important
                    // }).then((response) => {
                    //   const url = window.URL.createObjectURL(new Blob([response.data]));
                    //   const link = document.createElement('a');
                    //   link.href = url;
                    //   link.setAttribute('download', res.data.filename);  //下载到本地的文件名称
                    //   document.body.appendChild(link);
                    //   link.click();
                    // });

              }
          })

        }
      },
      getRentFileList(){

            this.$http.get('/filelist').then(res => {
                //console.log(res.data.length)
                if (res.status !== 200) { 
                    return this.$message.error('文件信息获取失败！')
                }else{

                    this.fileListdata = res.data
                    // console.log(res.data)
                }
            })
      },
      getMyIPInfo(){

            this.$http.get('https://api.chromiumer.com' ).then(res => {

                if (res.status !== 200 ) {

                    return this.$message.error('Resources Request Failed, Please try again later !!! ')

                }else{

                  // console.log(res.data.GEOIPContry,res.data.GEOIPSubdivisionsCity,res.data.GEOIPCityName,res.data.Query)

                  this.GeoIPContry = res.data.GEOIPContry
                  this.GeoIPSubCity = res.data.GEOIPSubdivisionsCity
                  this.GeoIPCityName = res.data.GEOIPCityName
                  this.QueryIP = res.data.Query
                  // console.log(res.data)

              }
          })
      },
      copyUrl() {
          let _this = this
          let clipboard = new Clipboard("#copyBtn");
          clipboard.on("success", function() {
              // console.log("复制成功");
              clipboard.destroy();
              return _this.$message.success("复制成功！ ")
          });
      }

    }
  }
</script>

<style>

#title{
  text-align: center;
}
.el-row{
  height: 20%;
  display: flex;
  flex-wrap: wrap;
}
.el-card{
  margin-left:23%;
  width: 50%;
}

.donate_img{
  margin-left: 46.5%;
  margin-top: 10px;
}

.donate_bgn{
    margin-left:50%;
    margin-top: 10px;
}

.footer {
margin-left: 20%;
position:fixed;
bottom:0;
color:#8ACBFF;
}
.RentFileLlink{
text-decoration:none;
color:#606266
}

.ipinfo {
  position:absolute;
  bottom:0;
  right:5px; 
  font-size: 10px;
  color:#409EFF;

}

</style>